<template>
  <div>
    <div class="list-container fadeIn animated">
      <div class="content">
        <el-card class="item create" :body-style="{ padding: '0px' }" v-if="path == 'my'">
          <el-button type="text" class="create-btn" @click="create">
            <div class="el-icon-plus"></div>
            <span>新建</span>
          </el-button>
        </el-card>
        <el-card class="item" v-for="item in data" :key="item.sid" :body-style="{ padding: '0px', height: '100%' }">
          <div style="padding: 14px;">
            <span>{{item.title}}</span>
          </div>
          <div style="padding: 0 14px;" class="latest">
            <span>{{fromNow(item.update_time)}}</span>
          </div>
          <span class="status" :class="{'status-2': item.status == 1}"><i></i>{{item.status == 1 ? '已发布' : '未发布'}}</span>
          <div class="bottom action-area">
            <el-dropdown>
              <span class="el-dropdown-link">
                操作<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item icon="el-icon-edit" @click.native="edit(item)">编辑</el-dropdown-item>
                <el-dropdown-item icon="el-icon-document-copy" @click.native="copySurvery(item.sid)">复制</el-dropdown-item>
                <el-dropdown-item icon="el-icon-delete" @click.native="deleteSurvery(item.sid)">删除</el-dropdown-item>
                <el-dropdown-item icon="el-icon-view" @click.native="viewAddr(item.sid)">查看网址</el-dropdown-item>
                <el-dropdown-item icon="el-icon-setting" @click.native="manage(item.sid)">权限管理</el-dropdown-item>
                <el-dropdown-item icon="el-icon-data-line" @click.native="statis(item.sid)">统计报表</el-dropdown-item>
                <!-- <el-dropdown-item icon="el-icon-bottom" @click.native="exportData(item.sid)">导出数据</el-dropdown-item> -->
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-card>
        <el-pagination
          class="pager"
          background
          layout="prev, pager, next"
          :hide-on-single-page="totalPage <= 1"
          :current-page.sync="page"
          @current-change="getList"
          :page-count="totalPage">
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="问卷地址"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      width="30%">
      <div style="display: flex">
        <el-input style="margin-right: 10px;" :value="addr" readonly="readonly"></el-input>
        <el-button class="ml10" size="medium" type="primary"
          v-clipboard:copy="addr"
          v-clipboard:success="onCopy"
          v-clipboard:error="onError">点击复制</el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="权限管理"
      :visible.sync="settingDialogVisible"
      width="30%">
      <div style="display: flex">
        <el-tag
          style="margin-right: 10px"
          :key="tag"
          v-for="tag in tags"
          closable
          :disable-transitions="false"
          @close="handleClose(tag)">
          {{tag}}
        </el-tag>
        <el-input
          style="width: 90px;"
          v-if="inputVisible"
          class="input-new-tag"
          size="small"
          v-model="inputValue"
          ref="saveTagInput"
          @keyup.enter.native="handleInputConfirm"
          @blur="handleInputConfirm"
        >
        </el-input>
        <el-button v-else size="small" @click="showInput">+ 添加管理员</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import api from '~/api/api';
  import * as types from '~/store/mutation-type';
  import store from '~/store/index';
  // import relativeTime from 'dayjs/plugin/relativeTime'

  import dayjs from 'dayjs';
  import relativeTime from 'dayjs/plugin/relativeTime';
  import 'dayjs/locale/zh-cn';
  dayjs.locale('zh-cn');
  dayjs.extend(relativeTime);

  export default {
    data() {
      return {
        path: 'my',
        data: null,
        page: 1,
        totalPage: 1,
        dialogVisible: false,
        settingDialogVisible: false,
        inputVisible: false,
        inputValue: '',
        addr: '',
        sid: '',
        tags: []
      };
    },
    mounted() {
      this.path = this.$route.fullPath.split('/')[2];
      this.getList();
    },
    methods: {
      exportData() {
        api.exportData();
      },
      handleClose(tag) {
        api.deleteRole(this.sid, tag).then(res => {
          this.tags.splice(this.tags.indexOf(tag), 1);
          this.inputVisible = false;
          this.inputValue = '';
        });
      },

      showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },

      handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
          api.addRole(this.sid, inputValue).then(res => {
            this.tags.push(inputValue);
            this.inputVisible = false;
            this.inputValue = '';
          })
        } else {
          this.inputVisible = false;
          this.inputValue = '';
        }
      },

      onCopy() {
        this.$alert('复制成功', '提示', {
          confirmButtonText: '确定',
          callback: action => {
            this.dialogVisible = false;
          }
        });
      },
      viewAddr(sid) {
        let baseUrl = process.env.NODE_ENV == 'production' ? 'https://siteres.ztgame.com/survey/fe/#/site/' : 'https//survey.dev.ztgame.com/fe/#/site/';
        this.addr = baseUrl + sid;
        this.dialogVisible = true;
      },
      statis(sid) {
        this.$router.push({ path: `/statis/${sid}` });
      },
      manage(sid) {
        this.sid = sid;
        api.getAdminList(sid).then(res => {
          this.tags = res.data;
          this.settingDialogVisible = true;
        })
      },
      create() {
        store.commit(types.INIT_QUESTIONS);
        this.$router.push({ path: '/edit' });
      },
      fromNow(time) {
        return dayjs(time).fromNow();
      },
      deleteSurvery(sid) {
        this.$confirm('确定要删除吗？', '删除问卷确认', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          return api.deleteSurvery(sid).then(res => {
            if (res) {
              return this.getList().then(() => {
                this.$notify({
                  offset: 50,
                  title: '删除成功',
                  type: 'success'
                });
              });
            }
          });
        });
      },
      copySurvery(sid) {
        api.copySurvery(sid).then(res => {
          if (res) {
            store.commit(types.INIT_QUESTIONS);
            this.$router.push({ path: `/edit/${res.sid}` })
          }
        });
      },
      getList() {
        let commissioned = this.path == 'my' ? null : 1;
        let number = this.path == 'my' ? 11 : 12;
        let _this = this;
        return new Promise((resolve, reject) => {
          api.getList(this.page, number, commissioned).then(res=> {
            _this.data = null;
            if (res) {
              _this.data = JSON.parse(JSON.stringify(res.data));
              _this.totalPage = res.pagecount
            }
            resolve();
          })
        });
      },
      edit(item) {
        if(item.status == 1) {
          this.$confirm('你修改问卷时，问卷会暂停发布，你需在修改完后再次发布问卷才可继续，原问卷链接不会改变?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            return api.updateSurvery({sid: item.sid, status: "0"});
          }).then(res => {
            if (res) {
              store.commit(types.INIT_QUESTIONS);
              this.$router.push({ path: `/edit/${item.sid}` });
            }
          });
        } else {
          store.commit(types.INIT_QUESTIONS);
          this.$router.push({ path: `/edit/${item.sid}` });
        }
      },
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .list-container {
    width 1306px
    margin 0 auto
    position relative
    .pager {
      position absolute
      bottom -40px
      right 0
    }
    .content {
      margin-top 22px;
      height: 670px;
      .item {
        width 306px
        height 190px
        padding 15px
        margin-top 20px
        margin-left 17px
        float left
        position relative
        &:hover {
          border: 2px solid #2672ff;
          box-shadow: 0px 0px 5px #838383;
        }
        .latest {
          font-size 14px;
          color grey
        }
        .status {
          color: grey;
          display: inline-block;
          position absolute
          font-size 14px
          bottom 26px
          left 29px
          &-2 {
            color #68bb47
            i {
              background-color: #68bb47 !important;
              box-shadow: 0 0 4px 0 #68bb47 !important;
            }
          }
          i {
            display: inline-block;
            float: left;
            margin-top: 7px;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: grey;
            margin-right: 7px;
            box-shadow: 0 0 4px 0 grey;
          }
        }
        &.create {
          vertical-align middle
          font-size 20px
          .create-btn {
            width 276px;
            height: 160px;
            font-size 20px
            color: #2672FF;
            text-align: center;
            vertical-align: middle;
            position: relative;
          }
        }
        .action-area {
          position absolute
          bottom 26px
          right 29px
        }
        .el-dropdown-link {
          cursor: pointer;
          color: #2672FF;
        }
      }
    }
  }
</style>



